@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap');


body{
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    background-color: black;
    height: 100%;
}

html,
body,
#root,
.container{
    height: 100%;
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 25px;
    font-weight: 900;
}


.movies{
    display: flex;
    flex-direction: column;
    padding: 50px;
    justify-content: space-between;
    align-items: center;
    margin: 5%;
    width: 80%;
}

.movies .movie{
    width: 800px;
    background-color: #e9ecef;
    margin-bottom: 40px;
    margin-top: 40px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 900;
}

.movie img{
    position: relative;
    top: -50px;
    left: -50px;
    max-width: 200px;
    margin-top: 10px;
    margin-left: 10px;
}

.movie .movie_data{
    margin-left: 20px;
}

.movie .movie_title{
    font-size: 28pt;
    font-weight: 1000;
    margin: 10px;
    margin-left: 0;
}

.movie .movie_year{
    margin: 10px;
    padding: 0;
    margin-left: 0;
}

.movie .movie_genres{
    list-style: none;
    color: red;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 10px;
    margin-left: 0px;
    font-size: 20px;
}


.movie .movie_genres li{
    margin-right: 10px;
};